/* eslint-disable react-hooks/exhaustive-deps */
import { useState, useEffect, useRef, lazy } from 'react';
import PropTypes from 'prop-types';
import Loadable from 'ui-component/Loadable';

// project imports
import ProductTable from './ProductTable';
import productFn from './productFn'; //functions
const PhotoSelector = Loadable(lazy(() => import('./PhotoSelector')));
const ProductSelector = Loadable(lazy(() => import('./ProductSelector')));

// material-ui
import { Button, FormGroup, FormControlLabel, Input, Tooltip, Typography, TextField, Checkbox, Autocomplete, Chip } from '@mui/material';
import { GridOn as GridOnIcon, Check as CheckIcon, Cancel as CancelIcon, CloudQueue as CloudQueueIcon } from '@mui/icons-material';

const ProductUpload = ({ langPack, localization, categoryId, groupId, referredRealProductId, rtsProduct }) => {
    console.log('product upload running');

    //data grid 
    let [tableColumns, setTableColumns] = useState(columnFormatter());
    let [tableRows, setTableRows] = useState(getSheetExample());
    let [columnVisibilityModel, setColumnVisibilityModel] = useState({});
    let [loadingProgress, setLoadingProgress] = useState(100); //loading
    let [page, setPage] = useState(0);
    let [page_size, setPage_size] = useState(10);
    let [total, setTotal] = useState(0);
    let [sheetRowSelected, setSheetRowSelected] = useState([]);

    //
    let [photoDialogOpen, setPhotoDialogOpen] = useState(false); //

    //
    let [productDialogOpen, setProductDialogOpen] = useState(false); //
    let [productTemplateSchema, setProductTemplateSchema] = useState(''); //
    let [showKeys, setShowKeys] = useState(false); //
    let [allKeyArr, setAllKeyArr] = useState([]); //key
    let [usedKeyArr, setUsedKeyArr] = useState([]); //key

    //
    let _sheetFile; //
    let uploaderRef = useRef(null); //

    useEffect(() => {
        if (!categoryId) return;
        makeProductInfo({ cat_id: categoryId }); //schemaproductFn
        return; //
    }, [categoryId]);

    useEffect(() => {
        if (!productTemplateSchema) return;
        console.log('new template found');
        let obj = productTemplateSchema.children[0].toObject();
        if (!obj) return;
        let arr = obj.field.map((subObj) => {
            let { id, name } = subObj;
            return { id, name };
        });
        arr = arr.filter((obj) => obj.name);
        arr = arr.filter((obj) => obj.id !== 'infos');
        arr = arr.filter((obj) => obj.id !== 'ApiPostLevelAttrAdapter');
        let newObj = {};
        arr.forEach((subObj) => (newObj[subObj.name.trim()] = subObj));
        arr = Array.from(Object.values(newObj));
        setAllKeyArr(arr);
        setShowKeys(true); //
        return; //
    }, [productTemplateSchema]);

    //____________
    async function makeProductInfo(obj) {
        let paramObj = ma.productSchemaQuery({
            param_product_top_publish_request: JSON.stringify({
                ...obj,
                language: localization.locale === 'enUS' ? 'en_US' : 'zh'
            })
        });
        let msg = await fetchAPI(paramObj);
        if (!msg || !msg.response.status) return queryResult(false, langPack.common.netFail + '\n' + msg ? msg.response.result : '');
        let res = ma.productSchemaCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        let schemaResult = productFn.schemaMaker(result);
        if (!schemaResult) return queryResult(false, langPack.steps.uneditable);
        //console.log(schemaResult);
    } // 
    //
    async function makeProductExample(obj) {
        let paramObj = ma.productSchemaRenderQuery({
            param_product_top_publish_request: JSON.stringify({
                ...obj,
                language: localization.locale === 'enUS' ? 'en_US' : 'zh'
            })
        });
        let msg = await fetchAPI(paramObj);
        if (!msg.response.status) return queryResult(false, msg.response.result);
        let res = ma.productSchemaRenderCheck(msg);
        let { status, result } = res.response;
        if (!status) return queryResult(status, result);
        let schemaResult = productFn.schemaMaker(result, true); //
        if (!schemaResult) return queryResult(false, langPack.steps.uneditable);
        setProductTemplateSchema(schemaResult);
    } //   &  

    function needSheet() {
        uploaderRef.current.click();
    } //

    function onFileSelected(e) {
        _sheetFile = null;
        let files = e.target.files;
        if (files && files.length) {
            for (let i = 0; i < files.length; i++) {
                if (files[i].size > 1 * 1000 * 1000) {
                    return queryResult(false, '1M');
                }
                _sheetFile = files[i];
                //console.log(_sheetFile);
                return fileRead(e);
            }
        }
    } //

    async function fileRead(e) {
        let res = await _sheetFile.text();
        let sheetRows = res.split(/\r\n/g);
        let headerArr = [];
        let bodyArr = [];
        if (sheetRows.length > 1) {
            let row = sheetRows[0].split(/,/g);
            let rowArr = [];
            let allHeaderArr = columnFormatter();
            for (let index = 0; index < row.length; index++) {
                let text = row[index].trim();
                let headerObj = allHeaderArr.find((obj) => obj.headerName == text);
                if (!headerObj) return queryResult(false, langPack.steps.columnNotFound);
                rowArr.push(headerObj.field);
            }
            headerArr = rowArr;
        } // head
        let rowLengthCheck;
        for (let i = 1; i < sheetRows.length; i++) {
            let row = sheetRows[i].split(/,/g);
            if (rowLengthCheck !== undefined) {
                if (row.length !== rowLengthCheck || row.length != headerArr.length) {
                    e.target.value = '';
                    return queryResult(false, langPack.steps.bulkUploadSheetMalformed);
                }
                rowLengthCheck = row.length;
            }
            if (row.length === 1) {
                break; //
            }
            let rowObj = { id: i - 1 };
            for (let index = 0; index < row.length; index++) {
                let text = row[index].trim();
                rowObj[headerArr[index]] = text;
            }
            bodyArr.push(rowObj);
        } // body
        console.log('file change', headerArr, bodyArr);
        setTotal(bodyArr.length);
        setTableRows(bodyArr);
        return;
    } //

    const needPost = async (productArr) => {
        console.log('need upload', productArr);
        if (!productArr.length) return queryResult(false, langPack.steps.productSheetEmpty);
        if (!categoryId) return queryResult(false, langPack.steps.productCategoryNeeded);
        setLoadingProgress(0);
        let count = 0;
        for (let productObj of productArr) {
            let postResult = await postProduct(productObj);
            console.log(postResult);
            count++;
            setLoadingProgress((count / productArr.length) * 100);
            await sleep(3000);
        }
    }; //

    async function postProduct(productObj) {
        console.log('start posting...');
        let requiredArr = ['subject', 'keyword_1', 'imageArr']; //
        let sharedArr = usedKeyArr.length ? usedKeyArr.map((str) => allKeyArr.find((obj) => obj.name == str).id) : []; //
        if (requiredArr.find((str) => !productObj[str])) return queryResult(false, langPack.steps.keyAttributeMissing);
        let {
            imageArr,
            subject,
            keyword_1,
            keyword_2,
            keyword_3,
            payment_methods,
            unit_type,
            logistic_mode,
            shipping_port,
            packaging_description
        } = productObj;
        let newProductObj = {};
        if (imageArr && imageArr.length) {
            let baseObj = productFn.imageObjMaker();
            let valueArr = imageArr;
            let newObj = productFn.imageObjChecker(baseObj, valueArr);
            newProductObj['scImages'] = newObj;
        }
        if (subject) {
            let baseObj = productFn.subjectObjMaker();
            let value = subject;
            let newObj = productFn.subjectObjChecker(baseObj, value);
            newProductObj['productTitle'] = newObj;
        }
        if (keyword_1) {
            let baseObj = productFn.keywordObjMaker();
            let valueArr = [keyword_1, keyword_2, keyword_3];
            valueArr = valueArr.filter((str) => str);
            let newObj = productFn.keywordObjChecker(baseObj, valueArr);
            newProductObj['productKeywords'] = newObj;
        }
        if (payment_methods) {
            let baseObj = productFn.paymentMethodObjMaker();
            let valueArr = [{ value: payment_methods }];
            let newObj = productFn.paymentMethodObjChecker(baseObj, valueArr);
            newProductObj['paymentMethod'] = newObj;
        }
        if (unit_type) {
            let baseObj = productFn.priceUnitObjMaker();
            let valueOption = baseObj._options.find((obj) => new RegExp(unit_type, 'gi').test(obj.key));
            if (!valueOption) return queryResult(false, langPack.steps.productPriceUnitNeeded);
            let valueObj = { value: valueOption.value };
            let newObj = productFn.priceUnitObjChecker(baseObj, valueObj);
            newProductObj['priceUnit'] = newObj;
        }
        if (logistic_mode) {
            let baseObj = productFn.logisticsObjMaker();
            let valueOption = baseObj._options.find((obj) => new RegExp(logistic_mode, 'gi').test(obj.key));
            let valueArr = valueOption ? [valueOption] : baseObj._options;
            valueArr = valueArr.map((obj) => obj.key);
            let newObj = productFn.logisticsObjChecker(baseObj, valueArr);
            newProductObj['logisticsMode'] = newObj;
        }
        if (shipping_port) {
            let baseObj = productFn.portObjMaker();
            let value = shipping_port;
            let newObj = productFn.portObjChecker(baseObj, value);
            newProductObj['port'] = newObj;
        }
        if (packaging_description) {
            let baseObj = productFn.packaingDescriptionObjMaker();
            let value = packaging_description;
            let newObj = productFn.packaingDescriptionObjChecker(baseObj, value);
            newProductObj['wholeSalePkgDesc'] = newObj;
        }
        if (groupId) {
            let baseObj = productFn.productGroupObjMaker();
            let value = groupId;
            let newObj = productFn.productGroupObjChecker(baseObj, value);
            newProductObj['productGroup'] = newObj;
        }
        if (rtsProduct) {
            let baseObj = productFn.productTypeObjMaker();
            let value = rtsProduct ? '1' : '2';
            let newObj = productFn.productTypeObjChecker(baseObj, value);
            newProductObj['market'] = newObj;
        }
        //
        for (let id of sharedArr) {
            if (!newProductObj[id]) {
                newProductObj[id] = { id, xml: productTemplateSchema.children[0].querySelector(`field#${id}`) };
            }
        }
        console.log(newProductObj);
        let editMode = 'add';
        let xmlString = productFn.schemaChecker(newProductObj);
        if (!xmlString) return queryResult(false, langPack.steps.modificationNull);
        let res = await productFn.schemaSubmit(xmlString, editMode, null, categoryId, localization.locale);
        if (!res || !res.response.status) return queryResult(false, langPack.common.netFail + '\n' + res ? res.response.result : '');
        queryResult(true, editMode === 'add' ? langPack.steps.addSuccess : langPack.steps.updateSuccess);
        return true;
        //
        //
    }

    //____________ 
    let productTableFn = { needPhoto, needSheet, needPost, needTemplate };

    const onTableSelection = (selectedProductArr) => {
        console.log(selectedProductArr.length + ' selected');
        return;
    }; // 

    function getSheetExample() {
        return [
            {
                id: 0,
                imageArr: '',
                subject: 'banana',
                keyword_1: 'fresh banana',
                keyword_2: 'Cavendish banana',
                keyword_3: 'Filipino banana',
                payment_methods: 'T/T',
                unit_type: 'piece',
                logistic_mode: 'sea',
                shipping_port: 'Manila',
                packaging_description: 'wrapped with carton'
            },
            {
                id: 1,
                imageArr: '',
                subject: 'strawberry',
                keyword_1: 'fresh strawberry',
                keyword_2: 'red strawberry',
                keyword_3: 'Spanish strawberry',
                payment_methods: 'L/C',
                unit_type: 'Kilogram',
                logistic_mode: 'sea',
                shipping_port: 'Vancouver',
                packaging_description: '10KG / box'
            }
        ];
    }

    function columnFormatter(obj) {
        const columns = [
            {
                field: 'imageArr',
                headerName: langPack.table.photos,
                disableExport: true,
                minWidth: 50,
                valueGetter: ({ row }) => row,
                valueFormatter: ({ value }) => value,
                renderCell: ({ value }) =>
                    value.imageArr && value.imageArr.length ? (
                        <img src={value.imageArr[0].url} style={{ width: '93%' }} alt="mainPic" />
                    ) : (
                        <CloudQueueIcon />
                    )
            },
            {
                field: 'subject',
                editable: true,
                headerName: langPack.table.subject
            },
            {
                field: 'keyword_1',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.keyword1
            },
            {
                field: 'keyword_2',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.keyword2
            },
            {
                field: 'keyword_3',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.keyword3
            },
            {
                field: 'payment_methods',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.payment_methods
            },
            {
                field: 'unit_type',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.unit_type
            },
            {
                field: 'logistic_mode',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.logistic_mode
            },
            {
                field: 'shipping_port',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.shipping_port
            },
            {
                field: 'packaging_description',
                editable: true,
                flex: 0.5,
                headerName: langPack.table.packaging_description
            }
        ];
        if (obj) {
        }
        return columns;
    } //

    const handleChangePage = (newPage) => {
        setPage(newPage);
    }; //

    const handleChangeRowsPerPage = (newSize) => {
        setPage_size(newSize);
    }; //

    const onRowClick = (obj, e) => {
        // let { id, row } = obj;
    }; //

    //____________ 
    function needPhoto(rowSelected) {
        console.log('needPhoto', rowSelected);
        setSheetRowSelected(rowSelected);
        setPhotoDialogOpen(true);
    } //

    const onPhotoConfirm = (valueArr) => {
        let newTableRows = [...tableRows];
        let obj = sheetRowSelected[0];
        let rowObj = { ...obj };
        let newValueArr = valueArr.map((obj) => {
            return { ...obj, key: obj.id, value: obj.url };
        });
        rowObj.imageArr = newValueArr;
        let { id } = rowObj;
        newTableRows[id] = rowObj;
        console.log('onPhotoConfirm', newTableRows);
        setTableRows(newTableRows);
    }; //

    //____________ 
    function needTemplate() {
        console.log('needTemplate');
        setProductDialogOpen(true);
    } //

    const onProductConfirm = (valueArr) => {
        let rowObj = valueArr[0];
        if (!rowObj) return;
        let { id } = rowObj;
        makeProductExample({ product_id: id }); //   &  
    }; //

    //____________JSX_______________________________________________

    return (
        <>
            <FormGroup>
                <FormControlLabel
                    ref={uploaderRef}
                    sx={{ display: 'none' }}
                    labelPlacement="top"
                    control={<Input type="file" />}
                    label={<GridOnIcon fontSize="large" />}
                    onChange={onFileSelected}
                />
            </FormGroup>
            <PhotoSelector
                onPhotoConfirm={onPhotoConfirm}
                dialogOpen={photoDialogOpen}
                setDialogOpen={setPhotoDialogOpen}
                langPack={langPack}
            />
            <ProductSelector
                onProductConfirm={onProductConfirm}
                dialogOpen={productDialogOpen}
                setDialogOpen={setProductDialogOpen}
                langPack={langPack}
            />
            <div hidden={showKeys ? false : true}>
                <Autocomplete
                    id="add-product-tempinfo"
                    sx={{ display: 'inline' }}
                    multiple
                    limitTags={9}
                    disableCloseOnSelect={true}
                    onChange={(e, value) => setUsedKeyArr(value)}
                    options={allKeyArr.length ? allKeyArr.map((obj) => obj.name) : []}
                    renderOption={(props, option, { selected }) => (
                        <li {...props}>
                            <Checkbox style={{ marginRight: 8 }} checked={selected} />
                            {option}
                        </li>
                    )}
                    renderTags={(options, getTagProps) =>
                        options.map((option, index) => <Chip variant="outlined" label={option} {...getTagProps({ index })} />)
                    }
                    renderInput={(params) => (
                        <TextField {...params} label={<Typography variant="h3">{langPack.steps.bulkUploadeTemplateDetails}</Typography>} />
                    )}
                />
            </div>
            <ProductTable
                tableRows={tableRows}
                tableColumns={tableColumns}
                page_size={page_size}
                page={page}
                total={total}
                localization={localization}
                langPack={langPack}
                columnVisibilityModel={columnVisibilityModel}
                setColumnVisibilityModel={setColumnVisibilityModel}
                handleChangePage={handleChangePage}
                handleChangeRowsPerPage={handleChangeRowsPerPage}
                onRowClick={onRowClick}
                productTableFn={productTableFn}
                onTableSelection={onTableSelection}
                loadingProgress={loadingProgress}
            />
        </>
    );
};

ProductUpload.propTypes = {
    langPack: PropTypes.object,
    localization: PropTypes.object,
    groupId: PropTypes.number,
    referredRealProductId: PropTypes.number,
    rtsProduct: PropTypes.bool
};

export default ProductUpload;
